import React, { Component } from "react";
import ReCAPTCHA from "react-google-recaptcha";

class ContactForm extends Component {
  state = {
    name: "",
    email: "",
    message: "",
    recaptchaToken: null,
    error: null,
    isSubmitting: false,
    isSuccess: false,
  };

  handleInputChange = (event) => {
    const { name, value } = event.target;
    this.setState({ [name]: value });
  };

  verifyCallback = (recaptchaToken) => {
    this.setState({ recaptchaToken });
  };

  handleSubmit = (event) => {
    event.preventDefault();

    const { name, email, message, recaptchaToken } = this.state;

    if (!recaptchaToken) {
      this.setState({ error: "Please complete the reCAPTCHA." });
      return;
    }

    this.setState({ isSubmitting: true });

    const options = { 
      method: "POST", 
      headers: {'Content-Type': 'application/x-www-form-urlencoded'},
      mode: 'cors',
      body: new URLSearchParams({
        secret: process.env.REACT_APP_CAPTCHA_SITE_KEY_VALIDATION,
        response: recaptchaToken
      }),
    };

    fetch("https://www.google.com/recaptcha/api/siteverify", options)
      .then((response) => {
        return response.json();
      })
      .then((data) => {
        const { success } = data;

        if (success) {
          // Token is valid, submit the form
          const formData = { name, email, message };
          // ...
          this.setState({ isSuccess: true });
        } else {
          // Token is invalid, show an error message
          this.setState({ error: "Invalid reCAPTCHA response." });
        }
      })
      .catch((error) => {
        console.error(error);
        this.setState({ error: "An error occurred. Please try again later." });
      })
      .finally(() => {
        this.setState({ isSubmitting: false });
      });
  };

  render() {
    const { name, email, message, error, isSubmitting, isSuccess } =
      this.state;

    if (isSuccess) {
      return <p>Thank you for your message!</p>;
    }

    return (
      <form onSubmit={this.handleSubmit}>
        <div>
          <label htmlFor="name">Name:</label>
          <input
            type="text"
            id="name"
            name="name"
            value={name}
            onChange={this.handleInputChange}
            required
          />
        </div>
        <div>
          <label htmlFor="email">Email:</label>
          <input
            type="email"
            id="email"
            name="email"
            value={email}
            onChange={this.handleInputChange}
            required
          />
        </div>
        <div>
          <label htmlFor="message">Message:</label>
          <textarea
            id="message"
            name="message"
            value={message}
            onChange={this.handleInputChange}
            required
          />
        </div>
        {error && <p className="error">{error}</p>}
        <ReCAPTCHA
          sitekey={process.env.REACT_APP_CAPTCHA_SITE_KEY}
          onChange={this.verifyCallback}
        />
        <button type="submit" disabled={isSubmitting}>
          Send
        </button>
      </form>
    );
  }
}

export default ContactForm;
